import React from 'react';
import Card from '@components/Card';
import {
  PostLayout,
  PostFileContent,
  PostMarkdownContent,
} from '@components/Pages/Post/Styles';
import Utterances from '@components/Utterances';
import { UTTERANCES_THEME } from '../../../constants/utterances';
import { ContentBox, AboutProfileImage } from './Styles';

interface Props {
  rootMarkdownHtml: string;
  profileUrl: string;
  utterancesRepo?: string;
}

function AboutPresenter({
  rootMarkdownHtml,
  profileUrl,
  utterancesRepo,
}: Props) {
  return (
    <ContentBox>
      <Card maxWidth="33%">
        <AboutProfileImage src={profileUrl} />
      </Card>

      {rootMarkdownHtml && (
        <Card title="소개">
          <PostLayout>
            <PostFileContent>
              <PostMarkdownContent>
                <div
                  style={{
                    padding: '1.25rem',
                  }}
                  dangerouslySetInnerHTML={{
                    __html: rootMarkdownHtml,
                  }}
                />
              </PostMarkdownContent>
            </PostFileContent>
          </PostLayout>
        </Card>
      )}

      {utterancesRepo && (
        <Card>
          <Utterances
            repo={utterancesRepo}
            theme={UTTERANCES_THEME['github-light']}
          />
        </Card>
      )}
    </ContentBox>
  );
}

export default AboutPresenter;
